

.notFound{
    width: 65%;
    background-image: linear-gradient(180deg, rgba(78, 205, 255, 0.589) 0%, rgba(145, 201, 253, 0.966) 100%);
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    border-radius: 15px;
    order: 2;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.message{
    margin-top: 20px;
    width: 90%;
    height: 150px;
}

.message h4{
    font-family: 'kalam';
    font-size: 25px;
    text-align: center;
    margin: 0;
}

.message h5{
    font-size: 25px;
    margin: 0;
    text-align: center;
    font-family: 'kalam';
    color: rgba(190, 19, 19, 0.795);
    margin-top: 15px;
}

.options{
    width: 90%;
}

.options h6{
    font-size: 20px;
    margin: 0;
    font-family: 'kalam';
    text-align: center;
}

.options .right{
    width: 100%;
    margin: 0px auto;
    margin-top: 20px;
}

.easter_egg{
    width: 90%;
    margin-top: 50px;
}

.easter_egg h6{
    font-size: 20px;
    margin: 0;
    font-family: 'kalam';
    text-align: center;
    margin-bottom: 10px;
}

.easter_egg p{
    width: 100%;
    text-align: center;
    margin: 0;
    font-family: 'kalam';
    margin-bottom: 10px;
}

.easter_egg p a{
    color: rgba(0, 0, 0, 0.733);
}

@media(max-width: 965px){
    .notFound{
        width: 100%;
        order: 1;
    }

    .options .right{
        margin-bottom: 20px;
    }

    .easter_egg{
        display: none;
    }
}