@font-face{
    font-family: 'Conformity';
    src: url('../../../font/CONFN___.TTF');
}

.mainbar{
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
    width: 96%;
    height: 150px;
    align-items: center;
    margin: 0px auto;
    margin-top: 10px;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    border-radius: 16px;
    background-image: linear-gradient(180deg, rgba(145, 201, 253, 0.966) 0%, #E3FFFF 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    
    
}

.left{
    list-style: none;
    display: flex;
    
    align-items: center;
    margin: 0;
    padding: 0;
    width: 34%;
    height: 100%;
}




.left .image{
    width: 30%;
    position: relative;
    height: 100%;
}
.left img{
    width: 100px;
    position: absolute;
    height: 100px;
    border-radius: 50%;
    z-index: 1;
    top: 13px;
    left: 13px;
}

.left .inner{
    position: absolute;
    width: 106px;
    height: 106px;
    background-color: #FFC187;
    top:10px;
    left:10px;
    border-radius: 50%;
}

.left .credits{
    width: 100%;
    position: absolute;
    top: 80%;
    
    text-align: center;
    font-size: 14px;
    font-family: 'Kalam';
}

.left .credits a{
    color: rgba(0, 0, 0, 0.568);
    text-decoration: none;
    transition: all 350ms;
}

.left .credits a:hover{
    color: black;
}

.left h1{
    font-family: 'Conformity';
    font-size: 40px;
    letter-spacing: 0.2em;
    color: rgb(173, 121, 53);
    user-select: none;
    
}

.right{
    list-style: none;
    margin:0;
    padding: 0;
    width: 60%;
    display: flex;
    justify-content: space-around;
}

.right .link{
    position: relative;
    width: 20%;
    height: 55px;
    
    
    
    
}

.right .link .main_link{
    font-family: 'kalam';
    width: 100%;
    height: 100%;
    background-image: linear-gradient(45deg, rgba(170,163,255,0.769) 0%, rgba(192,226,237,0.008) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0;
    background-color: #C0E2ED;
    color: #FFFFFF;
    border-radius: 10px;
    text-decoration: none;
    transition: all 250ms;
}

.right .link .main_link:hover{
    background-image: linear-gradient(45deg, rgba(98, 89, 201, 0.769) 0%, rgba(117, 169, 187, 0.008) 100%);
    transform: scale(1.1);
}

.right .link .text{
    user-select: none;
    width: 60%;
    padding-left: 10px;
    
}


.right .link img{
    position: absolute;
    top: 19px;
    right: 10px;
    width: 20px;
}

.right .link #twitter{
    top: 20px
}

.home_link{
    text-decoration: none;
}

/*
    Inicio de los breakpoints
*/

@media(max-width: 2000px){
    .left{
        width: 23%;
    }
}
@media(max-width: 1900px){
    .left{
        width: 25%;
    }
}


@media(max-width: 1800px){
    .left{
        width: 27%;
    }
}

@media(max-width: 1700px){
    .left{
        width: 27%;
    }
}

@media(max-width: 1620px){
    .left{
        width: 27%;
    }
}

@media(max-width: 1560px){
    .left{
        width: 30%;
    }
}
@media(max-width: 1500px){
    .left{
        width: 30%;
    }
}

@media(max-width: 1440px){
    .left{
        width: 34%;
    }
}





@media(max-width: 1254px){
    .left h1{
