        width: 40%;
    }
}

@media(max-width: 1000px){
    .left .image{
        width: 32%;
    }
}

@media(max-width: 965px){

    .mainbar{
        height: 300px;
        align-items: baseline;
    }
    .left{
        width: 100%;
        flex-flow: row wrap;
        height: 150px;
    }

    .left .credits{
        width: auto;
        left: 10px;
    }

    .left .image{
        display: flex;
        width: 100%;
        justify-content: center;
        
    }

    .left .inner, .left img{
        
        position: absolute;
        left: 44.6%;
    }

    .left img{
        left: 44.9%;
    }

    .left .credits{
        width: 100%;
    }

    .h1{
        width: 100%;
        text-align: center;
        position: relative;
        height: 44px;
    }

    .h1 h1{
        margin: 0;
    }

    .right{
        width: 100%;
    }

    
}

@media(max-width: 760px){
    .left img{
        left: 45%;
    }
}

@media(max-width: 740px){

