        left: 42%;
    }

    .left .inner{
        left:  41.5%;
    }
    .left .credits{
        left: 0px;
    }

    .left .home_link{
        width: 100%;
        position: absolute;
        left: 0;
    }
}

@media(max-width: 585px){

