.clips_section{
    width: 65%;
    background-image: linear-gradient(180deg, rgba(162, 109, 248, 0.445) 0%, rgba(145, 201, 253, 0.966) 100%);
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    border-radius: 15px;
    order: 2;
}

.clips_section h2{
    text-align: center;
    font-size: 35px;
}

.clips{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}

.clips .clip{
    width: 30%;
    margin: 10px;
    margin-bottom: 30px;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    border-radius: 15px;
    display: flex;
    flex-flow: row wrap;
    transition: all 350ms;
    
    
}

.clips .clip:hover{
    transform: scale(1.1);
}

.clips .clip img{
    opacity: 0.8;
    width: 100%;
    border-radius: 15px;
}

.clips .clip .clip_text{
    display: block;
    font-weight: bold;
    font-size: 14px;
    width: 100%;
    text-align: center;
    padding: 10px;
}

.clips .clip .long{
    text-align: left !important;
}

.direct_twitch{
    width: 50%;
    height: 100px;
    margin: 0px auto;
    margin-top: 20px;
    text-align: center;
}

.direct_twitch a{
    position: relative;
    display: flex;
    justify-content: center;
    color: white;
    text-decoration: none;
    
    width: 100%;
    height: 100%;
    background-image: linear-gradient(45deg, rgba(123, 77, 209, 0.769) 0%, rgba(90, 168, 194, 0.527) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    margin: 0px auto;
    text-align: center;
    border-radius: 15px;
    transition: all 250ms;
    align-items: center;
}

.direct_twitch a:hover{
    background-image: linear-gradient(45deg, rgba(144, 84, 255, 0.769) 0%, rgba(130, 224, 255, 0.527) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    transform: scale(1.1);
}

.direct_twitch a .text{
    font-family: 'kalam';
    font-size: 20px;
    
    
}

/*Inicio del responsive twitch*/

@media(max-width: 2000px){
    .direct_twitch{
        margin-bottom: 25px;
    }
}

@media(max-width: 965px){
    .clips_section{
        width: 100%;
        order: 1;
    }

    .direct_twitch{
        margin-bottom: 25px;
    }
}

@media(max-width: 690px){
    .direct_twitch{
        width: 65%;
    }
}

@media(max-width: 630px){
    .clips .clip{
        width: 27%;
    }

    .clip a{
        height: 90px;
    }

    .clips .clip .long{
        text-align: center !important;
    }

    .clips .clip .clip_text{
        padding: 0;
        font-size: 13px;
        
    }
}

@media(max-width: 530px){
    .clips{
        overflow-y: scroll;
        height: 650px;
    }

    .clips .clip{
        width: 45%;
        height: 170px;
    }

    .clips .clip a{
        display: flex;
        height: 130px;
    }
    
}

@media(max-width: 430px){
    .clips .clip a{
        display: block;
        width: 100%;
    }

    .clip .clip_text{
        margin-top: -20px;
    }
}

@media(max-width: 417px){
    .clips .clip{
        width: 42%;
        height: 140px;
    }

    .clip .clip_text{
        margin-top: -30px;
    }
}

