@font-face{
    font-family: 'Conformity';
    src: url('../../../font/CONFN___.TTF');
}

.footer{
    
    
    width: 96%;
    height: 400px;
    display: flex;
    flex-flow: row wrap;
    margin: 0px auto;
    margin-top: 10px;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    border-radius: 16px;
    background-image: linear-gradient(180deg, rgba(145, 201, 253, 0.966) 0%, #ff8d8dbe 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    margin-bottom: 10px;
}

.main_one{
    display: flex;
    flex-flow: row wrap;
    padding: 0;
    width: 90%;
    border-bottom: 1px solid #ffff;
    margin: 0px auto;
    height: 130px;
    justify-content: space-between;
}

.footer .main_name{
    display: flex;
    justify-content: space-between;
    
    width: 32%;
}

.main_name .image{
    position: relative;
    width: 10%;
    height: 100px ;
    align-self: flex-end;
}

.main_name h1{
    font-family: 'Conformity';
    font-size: 40px;
    letter-spacing: 0.2em;
    color: rgb(173, 121, 53);
    user-select: none;
    padding: 0;
    margin: 0;
    margin-top: 45px;
    align-self:flex-end;
}

.main_name .image img{
    width: 80px;
    position: absolute;
    height: 80px;
    border-radius: 50%;
    z-index: 1;
    top: 13px;
    left: 13px;
}

.main_name .image .inner{
    position: absolute;
    width: 86px;
    height: 86px;
    background-color: #FFC187;
    top:10px;
    left:10px;
    border-radius: 50%;

}

.email{
    width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: row wrap;


}

.email a{
    font-family: 'kalam';
    width: 35%;
    height: 50px;
    background-image: linear-gradient(45deg, rgba(252, 136, 165, 0.769) 0%, rgba(20, 71, 88, 0.008) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0;
    background-color: #C0E2ED;
    color: #FFFFFF;
    border-radius: 10px;
    text-decoration: none;
    transition: all 250ms;
    position: relative
}

.email a:hover{
    background-image: linear-gradient(45deg, rgba(197, 43, 82, 0.769) 0%, rgba(192,226,237,0.008) 100%);
    transform: scale(1.1);
}

.email a span{
    user-select: none;
    width: 60%;
    padding-left: 10px;
}

.email a img{
    position: absolute;
    top: 15px;
    right: 10px;
    width: 20px;
}

.email .extra{
    display: block;
    font-size: 14px;
    width: 100%;
}

.icons_footer{
    width: 90%;
    list-style: none;
    display: flex;
    flex-flow: row wrap;
    margin: 0;
    padding: 0;
    margin: 0px auto;
    margin-top: 10px;
    border-bottom: 1px solid #fff;
    
}

.socials, .arts{
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
}

.socials h4, .arts h4{
    font-family: 'kalam';
    user-select: none;
    color: #ffffff;
    margin: 0;
    padding: 0;
    text-align: center;
    height: 40px;
    width: 10%;
    font-size: 25px;
}

.socialMedia, .Art{
    list-style: none;
    padding: 0;
    margin: 0;
    width: 70%;
    display: flex;
    justify-content: space-around;
}



.socialMedia li, .Art li{
    width: 20%;
    height: 60px;
}

.socialMedia a, .Art a{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 16px;
    background-image: transparent;
    background-position: 0% 0%;
    background-repeat: repeat;
    transition: all 250ms;
}

#Twitch:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(178, 62, 201, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}

#YouTube:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(184, 41, 65, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}

#Twitter:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(36, 129, 184, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}

#TikTok:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(201, 62, 62, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}

#ArtStation:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(0, 70, 110, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}

#Reddit:hover{
    transform: scale(1.1);
    background-image: linear-gradient(45deg, rgba(212, 146, 23, 0.769) 0%, rgba(238, 235, 44, 0.466) 100%);
}


.socialMedia a img, .Art a img{
    width: 50px;
    height: 50px;
   
}


.LawCredits{
    width: 100%;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.LawCredits h1{
    font-weight: normal;
    

    margin: 0;
    padding: 0;
    font-size: 16px;
}

.LawCredits h1 a{
    font-family: 'kalam';
    color: rgba(0, 0, 0, 0.568);
    text-decoration: none;
    transition: all 250ms;
}

.LawCredits h1 a:hover{
    color: black;
}


