.lateral{
    width: 30%;
    
    border-radius: 15px;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    background-image: linear-gradient(180deg, rgba(245, 255, 110, 0.671) 0%, rgba(145, 201, 253, 0.966) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    display: flex;
    flex-flow: row wrap;
    order: 1;
}

.lateral h2, .lateral h3, .lateral h4{
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-shadow: 0px 1px 6px #6073e088;
    width: 100%;
    font-size: 25px;
    text-align: center;
    margin-top: 29px;
}
.lateral h4, .lateral h5, .lateral h6{
    width: 90%;
    margin: 0px auto;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 17px;
    text-align: center;
}

.lateral .chia_image{
    width: 100%;
}

.lateral .chia_image img{
    display: block;
    width: 100%;
}

.lateral .chia_image .big{
    display: block;
   width: 50%;
   margin: 0px auto;
   
   margin-top: 50px;
   margin-bottom: 50px;
}

.lateral .discord_join, .lateral .merch, .lateral .gift, .lateral .tips .kofi, .lateral .tips .streamlabs{
    
    width: 30%;
    height: 50px;
    background-image: linear-gradient(45deg, rgba(123, 77, 209, 0.769) 0%, rgba(90, 168, 194, 0.527) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    margin: 0px auto;
    text-align: center;
    border-radius: 15px;
    transition: all 250ms;
}

.lateral .discord_join:hover{
    box-shadow: 0 0.5em 0.5em -0.4em rgb(128, 0, 70);
    transform: translateY(-0.25em)
    
}

.lateral .discord_join a, .lateral .merch a, .lateral .gift a, .lateral .tips .kofi a, .lateral .tips .streamlabs a{
    font-family: 'kalam';
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: white;
    text-decoration: none;
}

.lateral .merch{
    background-image: linear-gradient(45deg, rgba(224, 173, 61, 0.769) 0%, rgba(55, 127, 151, 0.527) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    transition: all 350ms;
}

.lateral .merch:hover{
    box-shadow: 
    inset -6em 0 0 0 rgb(128, 0, 70),
    inset 6em 0 0 0 rgb(128, 0, 70); 
    transform: scale(1.1);
}

.lateral .gift{
    background-image: linear-gradient(45deg, rgba(194, 238, 74, 0.769) 0%, rgba(22, 99, 124, 0.527) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    transition: all 350ms;
}

.lateral .gift:hover{
    box-shadow: inset 0 0 0 2em rgb(86, 153, 161);
    transform: scale(1.1);
}

.lateral .tips{
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.lateral .tips .kofi{
    background-image: linear-gradient(45deg, rgba(136, 25, 25, 0.829) 0%, rgba(40, 192, 159, 0.418) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    transition: all 350ms;
    
}

.lateral .tips .kofi:hover{
    transform: scale(1.1);
    animation: pulse 1s ;
    box-shadow: 0 0 0 2em transparent;
}

@keyframes pulse{
    0% { box-shadow: 0 0 0 0 rgba(168, 23, 23, 0.377); }
}

.lateral .tips .streamlabs{
    position: relative;
    background-image: linear-gradient(45deg, rgba(28, 161, 161, 0.829) 0%, rgba(0, 0, 0, 0.418) 100%);
    background-position: 0% 0%;
    background-repeat: repeat;
    transition: all 350ms;
    overflow: hidden;
}

.lateral .tips .streamlabs:hover{
    transform: scale(1.1);
}

.lateral .tips .streamlabs a{
    position: relative;
    z-index: 1
}

.lateral .tips .streamlabs .inner_one{
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: 0;
    background-color: rgba(219, 145, 34, 0.479);
    transition: all 350ms;
    border-radius: 15px;
    
}

.lateral .tips .streamlabs:hover .inner_one{
    
    margin-left: calc(100% - 250px);
    margin-top: calc(50px - 75px) ;
    width: 400%;
    height: 100%;
    
}

/*Inicio del responsive lateral*/

@media(max-width: 1000px){

    .discord_main{
        display: flex;
        flex-flow: row wrap;
    }
    .lateral h2, .lateral h3, .lateral h4{
        margin-left: 10px;
        margin-right: 10px;
        font-size: 22px;
    }

    .lateral .discord_join, .lateral .merch, .lateral .gift, .lateral .tips .kofi, .lateral .tips .streamlabs{
        width: 35%;
    }
}

@media(max-width: 965px){

    .lateral{
        width: 100%;
        margin-top: 15px;
        order: 2;
    }

    .lateral h4{
        margin: 0px auto;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .discord_main{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
    }

    .lateral .merch:hover{
        box-shadow: inset -10em 0 0 0 rgb(128, 0, 70), inset 10em 0 0 0 rgb(128, 0, 70);
        transform: scale(1.1);
    }

    .big_boi_monei{
        margin: 0px auto;
        margin-top: 20px;
        width: 90%;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
    }

    
    .main_gift{
        display: flex;
        flex-flow: row wrap;
        width: 40%;
        justify-content: center;
        margin-top: 20px;
    }

    .main_gift h5{
        margin: 0;
        width: 100%;
        margin-bottom: 20px;
    }

    .main_gift .gift{
        margin: 0;
        width: 50%;
    }

    .main_tips{
        width: 42%;
        margin-top: 20px;
    }

    .main_tips h6{
        width: 100%;
        margin: 0;
        margin-bottom: 20px;
    }

    .main_tips .tips .kofi, .main_tips  .tips .streamlabs{
        margin: 0;
        width: 40%;
    }
    
    .chia_image{
        display: flex;
        flex-flow: row wrap;
        margin-top: 20px;
        justify-content: space-around;
    }
    .lateral .chia_image img{
        width: 70%;
        
    }

    .lateral .chia_image .big{
        width: 15%;
    }
}

@media(max-width: 600px){

    .big_boi_monei{
        width: 100%;
    }

    .main_tips{
        width: 47%;
    }
    .main_tips h6{
        font-size: 17px;
    }

    .main_tips .tips .kofi, .main_tips .tips .streamlabs {
        margin: 0;
        width: 45%;
    }
}

@media(max-width: 480px){
    .main_gift{
        width: 45%;
    }
}

@media(max-width: 440px){
    .main_gift .gift{
        width: 70%;
    }

    .main_tips .tips .kofi, .main_tips .tips .streamlabs{
        width: 47%;
    }
}

@media(max-width: 412px){
    .main_tips{
        width: 47%;
    }
}

@media(max-width: 405px){
    .main_gift h5{
        font-size: 15px;
    }

    .main_tips h6{
        font-size: 15px;
    }
}