.twitter_section{
    width: 65%;
    background-image: linear-gradient(180deg, rgba(78, 205, 255, 0.589) 0%, rgba(145, 201, 253, 0.966) 100%);
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    border-radius: 15px;
    order: 2;
}

h2{
    width: 100%;
    text-align: center;
    font-size: 25px;
}

.tweets{
    display: flex;
    width: 90%;
    margin: 0px auto;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
}

.tweets iframe{
    display: block;
    width: 50%;
    height: 300px;
    margin-top: 30px;
    border-radius: 16px;
    border: none;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
}

.follow{
    width: 50%;
    margin: 0px auto;
    height: 100px;
    background-image: linear-gradient(180deg, rgba(202, 151, 236, 0.445) 0%, #c4def8 100%);
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, .13), 0 1px 4px 0 rgba(0, 0, 0, .11);
    background-position: 0% 0%;
    background-repeat: repeat;
    background-size: cover;
    border-radius: 15px;
    border: none;
    margin-top: 20px;
    transition: all 250ms;
}

.follow:hover{
    transform: scale(1.1);
    background-image: linear-gradient(180deg, rgba(69, 169, 235, 0.445) 0%, #c4def8 100%);
}

.follow a{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-family: 'kalam';
    text-decoration: none;
    
}

@media(max-width: 1340px){
    .tweets iframe{
        height: 250px;
        margin-bottom: 25px;
    }

    
}

@media(max-width: 1095px){
    .tweets iframe{
        width: 65%;
        height: 265px;
    }

    .follow{
        width: 65%;
        margin-bottom: 15px;
    }
}

@media(max-width: 965px){
    .twitter_section{
        order: 1;
        width: 100%;
    }
}

@media(max-width: 663px){
    .tweets iframe{
        width: 70%;
    }

    .follow{
        width: 70%;
    }
}

@media(max-width: 620px){
    .tweets iframe{
        width: 85%;
    }

    .follow{
        width: 85%;
    }
}

@media(max-width: 510px){
    .tweets iframe{
        width: 90%;
    }

    .follow{
        width: 90%;
    }
}

@media(max-width: 480px){
    .tweets iframe{
        width: 100%;
    }

    .follow{
        width: 100%;
    }
}

@media(max-width: 430px){
    .twitter_section h2{
        font-size: 20px;
    }

    .tweets iframe{
        margin-top: 15px;
    }
}